import axios from 'axios'

const baseURL = process.env.NODE_ENV === 'production' ?
    `/api/pronogeeks` :
    `${process.env.REACT_APP_BACKENDPOINT}/api/pronogeeks`

const pronogeekService = axios.create({
    baseURL,
    withCredentials: true
})

export const savePronogeeks = async(homeScore, awayScore, fixtureID) => {
    const {
        data: {
            pronogeek: pronogeekExists
        }
    } = await pronogeekService.get(`/${fixtureID}`)
    if (!pronogeekExists) {
        const {
            data: {
                pronogeek
            }
        } = await pronogeekService.post(`/${fixtureID}`, {
            homeProno: homeScore,
            awayProno: awayScore
        })
        return pronogeek
    } else {
        const {
            data: {
                pronogeek
            }
        } = await pronogeekService.put(`/${pronogeekExists._id}`, {
            homeProno: homeScore,
            awayProno: awayScore
        })
        return pronogeek
    }
}